.select {
  cursor: pointer;
  width: 130px;
  background-color: #444;
  color: #fff;
  text-align: right;
  font-weight: bold;
  border-radius: 4px;
  border: 1px solid #fff;
}

.select__option {
  font-weight: bold;
}

.select__option:nth-child(even) {
  background-color: #555;
}
